.rts-single-shop-area {
    border: 1px solid rgba(32, 40, 45, 0.2);
    border-radius: 15px;
    display: block;
    overflow: hidden;
    transition: .3s;

    .inner-content {
        padding: 25px 30px;

        .stars-area {
            display: flex;
            align-items: center;
            gap: 5px;
            margin-bottom: 20px;

            i {
                color: var(--color-primary);
            }
        }

        a {
            .title {
                font-size: 24px;
                font-weight: 500;
            }
        }

        .pricing-area-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;

            p {
                margin: 0;
                font-size: 20px;
                font-weight: 700;
                color: var(--color-primary);
            }

            .rts-btn {
                height: 38px;
                border-radius: 5px;
                padding: 5px 15px;

                i {
                    font-size: 12px;
                    margin-right: 8px;
                }
            }
        }
    }

    &:hover {
        border: 1px solid transparent;
        background: #FFFFFF;
        box-shadow: 0px 30px 20px rgba(101, 101, 101, 0.1);

    }
}

.slider-pagination-area-shop-details {
    position: relative;

    .swiper-pagination {
        bottom: -160px;
        text-align: center;
        right: 50%;
        transform: translateX(50%);
        display: flex;
        left: auto;
        
        .swiper-pagination-bullet-active{
            border-width: 1px !important;
            border-style: solid !important;
            border-color: rgba(32, 40, 45, 0.301) !important;
            border-radius: 3px !important;
        }
        .swiper-pagination-bullet {
            padding: 10px;
            width: 120px;
            height: 120px;
            border-radius: 0;
            background-image: url(../images/shop/shop-03.jpg);
            border-width: 2px;
            border-style: solid;
            border-color: #f9f9f9;
            opacity: 1;
            background-size: cover;
            background-position: center;
            margin-right: 5px;

            &:first-child {
                background-image: url(../images/shop/shop-01.jpg);
            }

            &:last-child {
                background-image: url(../images/shop/shop-02.jpg);
            }
        }
    }
}

